<template>
  <img :src=src :width=width :height=height />
</template>

<script>
export default {
  props: {
    locus: {
      type: String,
      default: null
    },
    width:{
      type: String,
      default: ''
    },
    height:{
      type: String,
      default: ''
    }
  },

  computed: {
    src: function () {
      return this.$read(this.locus);
    }
  }
}
</script>
