<template>
  <div id="qrcode"></div>
</template>

<script>
export default {

  data: function () {
    return {
      qrHandler: null,
      privateComponentKey: 0
    };
  },

  computed: {
    doses: function () {
      return this.$read('doses');
    },
    componentKey: function (){
      return privateComponentKey;
    }
  },

  methods: {

    build: function () {
      var dose_template = 'DOSE #1: #company# Lot: #lot#\n' +
          'DATE: #date#\n';

      var doses = '';
      this.$read('doses').forEach(dose =>
          doses = doses.concat(dose_template)
              .replace('#company#', dose.company)
              .replace('#lot#', dose.lot)
              .replace('#date#', dose.date)
              .concat('\n')
      );

      var template = '\nSARS COVID 19\n' +
          'Immunization Record\n' +
          '\n' +
          'NAME: #firstName# #lastName#\n' +
          'SEX: #gender#  BIRTH: #dob#\n' +
          '\n' +
          '#doses#' +
          'Perfidious Immunization Services Inc.\n' +
          'MB CA\n ';

      template = template
          .replace('#firstName#', this.$read('txt.firstName'))
          .replace('#lastName#', this.$read('txt.lastName'))
          .replace('#gender#', this.$read('txt.gender'))
          .replace('#dob#', this.$read('txt.dob'))
          .replace('#doses#', doses);

      this.qrHandler = new QRCode(document.getElementById('qrcode'), {
        text: template,
        colorDark: "#000000",
        colorLight: "#ffffff"
      });
    }
  },

  mounted() {
    this.build();
  }

}
</script>