import Dose from "@/x-html/Dose";
import Doses from "@/x-html/Doses";
import Image from "@/x-html/Image";
import Input from "@/x-html/Input";
import Qrcode from "@/x-html/Qrcode";
import SocketStatus from "@/x-html/SocketStatus";
import TextHtml from "@/x-html/TextHtml";

let tags = {
    "dose": Dose,
    "doses": Doses,
    "image": Image,
    "input": Input,
    "qrcode": Qrcode,
    "socket-status": SocketStatus,
    "text-html": TextHtml,
}

const Manifest = Object.assign(
    ...Object
        .keys(tags)
        .map(key => ({[stateTagApp['namespace'].concat('-'.concat(key))]: tags[key]}))
);

export default Manifest;