<template>
  <div>
    <input type="date" v-model="date"/>
    <br>
    <select v-model="company">
      <option>Moderna</option>
      <option>Pfizer</option>
      <option>Janssen (J&J)</option>
      <option>AstraZeneca</option>
      <option>Serum Institute of India</option>
      <option>Sinopharm</option>
      <option>Sinovac</option>
    </select>
    <br>
    <input type="text" v-model="lot">
  </div>
</template>

<script>
export default {
  props:{
    i: {
      type: Number,
      required: true
    }
  },

  computed:{
    dose: function (){
      return this.$read('doses')[this.i.valueOf()];
    },

    date: {
      get: function() {
        return this.dose.date;
      },
      set: function (val){
        this.replaceDoseValueInDoses('date', val);
      }
    },
    company: {
      get: function() {
        return this.dose.company;
      },
      set: function (val){
        this.replaceDoseValueInDoses('company', val);
      }
    },
    lot: {
      get: function() {
        return this.dose.lot;
      },
      set: function (val){
        this.replaceDoseValueInDoses('lot', val);
      }
    },
  },

  methods:{
    replaceDoseValueInDoses: function (key, val){
      let doses = this.$read('doses');
      doses[this.i][key] = val;
      this.$write('doses', doses);
    }
  }
}
</script>